import type { Metadata } from "next";
import { Inter, Roboto_Mono } from "next/font/google"; // Switched to Inter for premium look
import "./globals.css";
import SmoothScroll from "@/components/SmoothScroll";
import Header from "@/components/Header";
import Footer from "@/components/Footer";

// Configure fonts
const inter = Inter({
  variable: "--font-inter",
  subsets: ["latin"],
  display: "swap",
});

const robotoMono = Roboto_Mono({
  variable: "--font-roboto-mono",
  subsets: ["latin"],
  display: "swap",
});

export const metadata: Metadata = {
  title: "Berejena Studio",
  description: "A cinematic web experience.",
};

import { CursorProvider } from "@/context/CursorContext";
import Cursor from "@/components/Cursor";

import { getProjects } from "@/app/actions/projects";
import { slugify } from "@/lib/utils";

// ... existing imports

export const dynamic = 'force-dynamic'; // Force dynamic rendering for admin updates to show immediately

export default async function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  // Fetch projects and generate categories
  const projects = await getProjects();
  const uniqueCategories = Array.from(new Set(projects.map(p => p.category)));

  const categoryLinks = uniqueCategories.map(cat => ({
    label: cat, // Keep original casing for label
    href: `/${slugify(cat)}`
  }));

  return (
    <html lang="en">
      <body
        className={`${inter.variable} ${robotoMono.variable} antialiased bg-background text-foreground overflow-x-hidden`}
      >
        <CursorProvider>
          <Cursor />
          <SmoothScroll>
            <Header categories={categoryLinks} />
            {children}
            <Footer />
          </SmoothScroll>
        </CursorProvider>
      </body>
    </html>
  );
}
